import style from "./SearchForm.module.scss";
// components
import Input from "../../../../shared/Input/Input";
import CheckBox from "../../../../shared/CheckBox/CheckBox";
import Select from "../../../../shared/Select/Select";
import Button from "../../../../shared/Button/Button";
// picker
import DatePicker from "react-date-picker";
// picker style
import "react-date-picker/dist/DatePicker.css";
import "react-calendar/dist/Calendar.css";
//
import { Controller, useForm } from "react-hook-form";
// helper
import validateINN from "../../../../helpers/validateINN";
//
import { useNavigate } from "react-router-dom";
import { objectSearch } from "../../../../service/api";

const SearchForm = () => {
  const {
    control,
    handleSubmit,
    reset,
    watch,
    formState: { errors, isDirty, isValid },
  } = useForm({
    mode: "onChange",
  });

  const navigate = useNavigate();

  const onHandleSubmit = (data) => {
    objectSearch.getData(data);
    objectSearch.getID(data);
    reset();
    navigate("/documents");
  };
  

  return (
    <form className={style.form} onSubmit={handleSubmit(onHandleSubmit)}>
      <div className={style.wrapper}>
        <Controller
          name="INN"
          control={control}
          rules={{
            validate: {
              required: (value) => {
                if (value) return validateINN(value);
                if (!value) return "  ";
              },
            },
            minLength: {
              value: 10,
              message: "    10  12 ",
            },
            maxLength: {
              value: 12,
              message: "    10  12 ",
            },
          }}
          defaultValue="7710137066"
          render={({ field: { onChange, value }, fieldState: { error } }) => (
            <Input
              label="  *"
              className={style.input}
              type="number"
              placeholder="10 "
              inputValue={value}
              onInputChange={(newVal) => {
                onChange(newVal);
              }}
              errorMassage={error?.message}
              error={error}
            />
          )}
        />

        <div className={style.selectLabel}>
           *
          <Controller
            name="tonality"
            control={control}
            defaultValue="any"
            rules={{ required: true }}
            render={({ field: { onChange, value } }) => (
              <Select
                className={style.select}
                options={[
                  { label: "", value: "any" },
                  { label: "", value: "positive" },
                  { label: "", value: "negative" },
                ]}
                onSelected={onChange}
                selected={value}
                name="select"
                id="select_tonality"
              />
            )}
          />
        </div>

        <Controller
          name="quantity"
          control={control}
          rules={{
            required: " ",
            minLength: 1,
            maxLength: 4,
            min: { value: 1, message: "   1" },
            max: { value: 1000, message: "   1000" },
          }}
          defaultValue="1000"
          render={({ field: { onChange, value }, fieldState: { error } }) => (
            <Input
              label="    *"
              className={style.input}
              type="number"
              placeholder=" 1  1000"
              inputValue={value}
              onInputChange={(newVal) => {
                onChange(newVal);
              }}
              errorMassage={error?.message}
              error={error}
            />
          )}
        />

        <div
          className={
            style.dateWrapper +
            " " +
            (errors.datePickerStart && errors.datePickerFinish
              ? style.error
              : "")
          }
        >
          <span>  *</span>
          <div className={style.pickerBox}>
            <div className={style.pickerWrapper}>
              <Controller
                name="datePickerStart"
                control={control}
                rules={{
                  required: "  ",
                }}
                defaultValue={new Date("2019/01/01")}
                render={({ field: { onChange, value } }) => (
                  <DatePicker
                    dateFormat="yyyy/MM/dd"
                    className={style.picker}
                    value={value}
                    maxDate={watch("datePickerEnd") || new Date()}
                    calendarIcon={null}
                    clearIcon={null}
                    onChange={onChange}
                  />
                )}
              />
            </div>
            <div className={style.pickerWrapper}>
              <Controller
                name="datePickerEnd"
                control={control}
                rules={{
                  required: "  ",
                }}
                defaultValue={new Date("2022/08/31")}
                render={({ field: { onChange, value } }) => (
                  <DatePicker
                    dateFormat="yyyy/MM/dd"
                    className={style.picker}
                    value={value}
                    maxDate={new Date()}
                    minDate={watch("datePickerStart") || new Date()}
                    calendarIcon={null}
                    clearIcon={null}
                    onChange={onChange}
                  />
                )}
              />
            </div>
          </div>
          <span className={style.errorMassage}>
            {errors.datePickerStart && errors.datePickerEnd
              ? "  "
              : "error"}
          </span>
        </div>
      </div>
      <div className={style.wrapper}>
        <Controller
          name="completeness"
          control={control}
          defaultValue={true}
          render={({ field: { onChange, value } }) => (
            <CheckBox
              onChange={onChange}
              isChecked={value}
              id="completeness"
              name="completeness"
            >
                
            </CheckBox>
          )}
        />

        <Controller
          name="businessContext"
          control={control}
          defaultValue={false}
          render={({ field: { onChange, value } }) => (
            <CheckBox
              onChange={onChange}
              isChecked={value}
              id="businessContext"
              name="businessContext"
            >
                -
            </CheckBox>
          )}
        />

        <Controller
          name="mainRole"
          control={control}
          defaultValue={true}
          render={({ field: { onChange, value } }) => (
            <CheckBox
              onChange={onChange}
              isChecked={value}
              id="mainRole"
              name="mainRole"
            >
                 
            </CheckBox>
          )}
        />

        <Controller
          name="riskFactors"
          control={control}
          defaultValue={false}
          render={({ field: { onChange, value } }) => (
            <CheckBox
              onChange={onChange}
              isChecked={value}
              id="riskFactors"
              name="riskFactors"
            >
                 -
            </CheckBox>
          )}
        />

        <Controller
          name="technicalNews"
          control={control}
          defaultValue={true}
          render={({ field: { onChange, value } }) => (
            <CheckBox
              onChange={onChange}
              isChecked={value}
              id="technicalNews"
              name="technicalNews"
            >
                 
            </CheckBox>
          )}
        />

        <Controller
          name="announcements"
          control={control}
          defaultValue={true}
          render={({ field: { onChange, value } }) => (
            <CheckBox
              onChange={onChange}
              isChecked={value}
              id="announcements"
              name="announcements"
            >
                 
            </CheckBox>
          )}
        />

        <Controller
          name="summaries"
          control={control}
          defaultValue={true}
          render={({ field: { onChange, value } }) => (
            <CheckBox
              onChange={onChange}
              isChecked={value}
              id="summaries"
              name="summaries"
            >
                
            </CheckBox>
          )}
        />
        <div className={style.submitWrap}>
          <Button type="submit" isDisabled={!isDirty || !isValid}>
            
          </Button>
          <p className={style.hint}>*    </p>
        </div>
      </div>
    </form>
  );
};
export default SearchForm;
