import style from "./SectionForm.module.scss";
//
import { Link } from "react-router-dom";
import { useForm, Controller } from "react-hook-form";
// service
import { onAuth } from "../../../../service/authorization";
// components
import Input from "../../../../shared/Input/Input";
import Button from "../../../../shared/Button/Button";
import Accounts from "../Accounts/Accounts";

const SectionForm = () => {
  const {
    handleSubmit,
    control,
    reset,
    getValues,
    formState: { isDirty, isValid },
  } = useForm({ mode: "onChange" });

  const onHandleSubmit = () => {
    onAuth(getValues("login"), getValues("password"));
    reset();
  };

  return (
    <form className={style.form} onSubmit={handleSubmit(onHandleSubmit)}>
      <div className={style.links}>
        <Link className={style.link + " " + style._active} to={"/login"}>
          
        </Link>
        <Link className={style.link} to={"/registration"}>
          
        </Link>
      </div>

      <Controller
        name="login"
        control={control}
        rules={{
          required: "  ",
          minLength: {
            value: 6,
            message: "  6 ",
          },
        }}
        defaultValue=""
        render={({ field: { onChange, value }, fieldState: { error } }) => (
          <Input
            label="   :"
            type="text"
            name="login"
            onInputChange={onChange}
            inputValue={value}
            errorMassage={error?.message}
            error={error}
          />
        )}
      />

      <Controller
        name="password"
        control={control}
        rules={{
          required: " ",
          minLength: {
            value: 6,
            message: "   6   ",
          },
        }}
        defaultValue=""
        render={({ field: { onChange, value }, fieldState: { error } }) => (
          <Input
            label=":"
            type="password"
            name="password"
            onInputChange={onChange}
            inputValue={value}
            errorMassage={error?.message}
            error={error}
          />
        )}
      />

      <Button isDisabled={!isDirty || !isValid} className={style.button}>
        
      </Button>
      <Link className={style.recover} to={"/recover%password"}>
         
      </Link>
      <Accounts />
    </form>
  );
};
export default SectionForm;
