import style from "./SectionTariff.module.scss";
import TariffCard from "../../../../shared/TariffCard/TariffCard";
//
import BeginnerIcon from "../../icons/beginner.png";
import ProIcon from "../../icons/pro.png";
import BusinessIcon from "../../icons/business.png";

const SectionTariff = () => {
  return (
    <section className={style.sectionTariff + " " + "container"}>
      <h2 className={style.title}> </h2>
      <div className={style.cardWrapper}>
        <TariffCard
          title="Beginner"
          description="  "
          icon={BeginnerIcon}
          backgroundColor="#FFB64F"
          price="799"
          lowPrice="1200"
          instalment="150"
          structureItem={[
            "  ",
            " ",
            " 24/7",
          ]}
        />
        <TariffCard
          checked={false}
          title="Pro"
          description=" HR  "
          icon={ProIcon}
          backgroundColor="#7CE3E1"
          price="1299"
          lowPrice="2600"
          instalment="279"
          structureItem={[
            "   Beginner",
            " ",
            "  ",
          ]}
        />
        <TariffCard
          checked={false}
          title="Business"
          description="  "
          icon={BusinessIcon}
          backgroundColor="#000000"
          color="#ffffff"
          price="2379"
          lowPrice="3700"
          structureItem={[
            "   Pro",
            "  ",
            " ",
          ]}
        />
      </div>
    </section>
  );
};
export default SectionTariff;
